import { closeModal, openModal } from './modal';
import { postData} from '../services/services';

function forms(formSelector) {

    // Forms
    const forms = document.querySelectorAll(formSelector);

    const message = {
        loading: 'img/form/spinner.svg',
        success: 'Спасибо! Скоро мы с вами свяжемся:)',
        failure: 'Что-то пошло не так...'
    };

    forms.forEach(item => {
        bindPostData(item);
    });



    function bindPostData(form) {
        form.addEventListener('submit', (e) => {
            e.preventDefault();

            const statusMessage = document.createElement('img');
            statusMessage.src = message.loading;
            // console.log(statusMessage.src);

            statusMessage.style.cssText = `
         display: block;
         margin: 0 auto;
         `;
            // form.append(statusMessage);
            form.insertAdjacentElement('afterend', statusMessage);
            /* const request = new XMLHttpRequest();
            request.open('POST', 'server.php'); */

            /* у input`ов, textarea, option`ов обязателен атрибут name */
            /* В зависимости от сервера, возможно нам нужно передавать данные в разных форматах */
            // request.setRequestHeader('Content-type', 'multipart/form-data');
            /* Если формат данных JSON */
            // request.setRequestHeader('Content-type', 'application/json');
            const formData = new FormData(form);

            /* создаем объект и записываем в него содержимое инпутов, каждый value
            с инпута в отдельную пару "ключ - значение" */
            const json = JSON.stringify(Object.fromEntries(formData.entries()));

            const object = {};
            formData.forEach(function (value, key) {
                object[key] = value;
            });

            /* превращаем объект в json */
            // const json = JSON.stringify(object);
            /* отправляем этот JSON */
            // request.send(json);
            /* отправляем formData */
            // request.send(formData);


            /*  fetch('server.php', {
                method: "POST",
                headers: {
                   'Content-type': 'application/json'
                },
                body: JSON.stringify(object)
             }) */
            postData('http://localhost:3000/requests', json)
                .then(data => {
                    console.log(data);
                    showThanksModal(message.success);
                    statusMessage.remove();
                }).catch(() => {
                    showThanksModal(message.failure);
                }).finally(() => {
                    form.reset();
                });

            /*   request.addEventListener('load', () => {
                 if (request.status === 200) {
                    console.log(request.response);
                    showThanksModal(message.success);
                    form.reset();
                    statusMessage.remove();
                 } else {
                    showThanksModal(message.failure);
                 }
              }); */

        });
    }



    function showThanksModal(message) {
        const prevModalDialog = document.querySelector('.modal__dialog');

        prevModalDialog.classList.add('hide');
        openModal('.modal', 'modalTimerId');

        const thanksModal = document.createElement('div');
        thanksModal.classList.add('modal__dialog');
        thanksModal.innerHTML = `
            <div div class="modal__content">
                <div class="modal__close" data-close>×</div>
                <div class="modal__title">${message}</div>
            </div>
            `;

        document.querySelector('.modal').append(thanksModal);
        setTimeout(() => {
            thanksModal.remove();
            prevModalDialog.classList.add('show');
            prevModalDialog.classList.remove('hide');
            closeModal('.modal');
        }, 4000);
    }

    // fetch('http://localhost:3000/menu')
    // .then(data => data.json())
    // .then(res => console.log(res));


    /*    fetch("https://jsonplaceholder.typicode.com/posts", {
          method: "POST",
          body: JSON.stringify({name: 'Alex'}),
          headers: {
             'Content-type': 'application/json'
          }
       })
          .then((response) => response.json())
          .then((json) => console.log(json)); */


}

export default forms;